import React from 'react'
import { graphql } from 'gatsby'
import Img from 'gatsby-image'
import styled from 'styled-components'
import Layout from '../components/layout'
import ScrollAnimation from 'react-animate-on-scroll'
import AniLink from 'gatsby-plugin-transition-link/AniLink'

const StyledHero = styled.div`
  display: flex;
  flex-direction: row;
  justiy-content: flex-start;
  max-width: 600px;
  margin: 6rem auto 2rem;

  .hero-left {
    width: min-content;
    margin-right: 2rem;
    min-width: 200px;
  }

  .hero-title,
  .hero-client {
    margin: 0;
  }

  .hero-title {
    font-size: 48px;
    span {
      text-transform: uppercase;
    }
  }

  .hero-information {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: flex-start;
    align-items: flex-start;

    em {
      font-size: 0.6rem;
      margin: 0;
      min-width: 50px;
      font-weight: 600;
    }
  }

  .hero-client {
    font-family: serif;
    margin-bottom: 1rem;
  }

  .hero-client,
  .hero-services {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;

    span {
      font-size: 0.8rem;
      text-transform: uppercase;
      font-weight: 100;
    }
  }

  .hero-services {
    ul {
      margin: 0;
    }

    &-list {
      display: flex;
      flex-direction: row;

      & > *:not(:last-child) {
        margin-right: 1rem;
      }

      li {
        line-height: 1;
      }
    }
  }
`

const Wrapper = styled.div`
  padding: 0 2rem;

  p {
    font-weight: 100;
  }

  .content-body {
    max-width: 600px;
    margin: 4rem auto;
  }
`

const PaginationWrapper = styled.div`
  .project-pagination {
    display: flex;
    flex-direction: row;
    width: 100%;

    @media (max-width: 768px) {
      flex-direction: column;
    }

    &-previous,
    &-next {
      flex-basis: 50%;
      position: relative;
      overflow: hidden;
      z-index: 4;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      @media (max-width: 768px) {
        flex-basis: 100%;
      }

      .gatsby-image-wrapper {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
      }

      &-title {
        padding: 4rem 2rem;
        z-index: 200;
        position: relative;
        text-align: center;
        line-height: 1;
      }

      &-background {
        position: absolute;
        top: 0;
        left: 0;
        background-size: cover;
        width: 100%;
        height: 100%;
        opacity: 0;
        z-index: 1;
        transition: all 0.3s ease-in-out;
        transform: scale(1.2);

        &:after {
          z-index: 0;
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 100%;
          background: rgba(40, 73, 48, 0.4);
        }
      }

      & .direction {
        display: block;
        position: absolute;
        z-index: 4;
        top: 0;
        width: 70px;
        left: calc(50% - 35px);
        top: 4rem;
        text-align: center;
      }

      & .left-arrow,
      & .right-arrow {
        display: block;
        position: absolute;
        top: calc(50% - 2.5rem);
        left: 0;
        z-index: 4;
        color: white;
        padding: 1rem;
        font-size: 2rem;
        color: black;
      }

      & .right-arrow {
        right: 0;
        text-align: right;
      }

      &:hover {
        .project-pagination-next-background {
          opacity: 1;
          transform: scale(1);
        }
        .project-pagination-previous-background {
          opacity: 1;
          transform: scale(1);
        }

        .project-pagination-previous-title,
        .project-pagination-next-title,
        .left-arrow,
        .right-arrow,
        .direction {
          color: #e2e2e2;
        }
      }
    }
  }
`

const StyledList = styled.ul`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  .image {
    padding: 1rem;
  }

  .image-half {
    flex-basis: 50%;
  }

  .image-full {
    flex-basis: 100%;
  }
`

const NavLink = styled(AniLink)`
  text-decoration: none;
  color: black;
  width: 100%;
`

const Project = ({ data, location, pageContext }) => {
  const {
    featuredImage,
    title,
    client,
    images,
    services,
  } = data.markdownRemark.frontmatter
  const { html } = data.markdownRemark

  return (
    <Layout themeColor="white">
      <StyledHero>
        <div className="hero-left">
          <h1 className="hero-title">{title}</h1>
        </div>
        <div className="hero-right">
          <div className="hero-information">
            <div className="hero-client">
              <em>Client:</em>
              <span>{client}</span>
            </div>
            <div className="hero-services">
              <em>Services:</em>
              <ul className="hero-services-list">
                {services &&
                  services.map(item => {
                    return (
                      <li>
                        <span>{item.service}</span>
                      </li>
                    )
                  })}
              </ul>
            </div>
          </div>
        </div>
      </StyledHero>
      <Img fluid={featuredImage.childImageSharp.fluid} />
      <Wrapper>
        <div
          className="content-body"
          dangerouslySetInnerHTML={{ __html: html }}
        ></div>
        <StyledList>
          {images.map(node => {
            const { image, image_alt, half_image } = node
            return (
              <li
                className={half_image ? 'image image-half' : 'image image-full'}
              >
                <ScrollAnimation key={`${image_alt}`} animateIn="fadeInUp">
                  <Img fluid={image.childImageSharp.fluid} alt={image_alt} />
                </ScrollAnimation>
              </li>
            )
          })}
        </StyledList>
      </Wrapper>
      <PaginationWrapper>
        <div className="project-pagination">
          <div className="project-pagination-previous">
            {data.previousProject && (
              <>
                <NavLink
                  cover
                  bg={'black'}
                  direction="right"
                  to={`/projects/${data.previousProject.frontmatter.slug}`}
                >
                  <h3 className="project-pagination-previous-title">
                    {data.previousProject.frontmatter.title}
                  </h3>
                </NavLink>
                <span className="direction">Previous</span>
                <span className="left-arrow">&larr;</span>
                <div
                  className="project-pagination-previous-background"
                  style={{
                    backgroundImage: `url(${data.previousProject.frontmatter.featuredImage.childImageSharp.original.src})`,
                  }}
                ></div>
              </>
            )}
          </div>
          <div className="project-pagination-next">
            {data.nextProject && (
              <>
                <NavLink
                  cover
                  bg={'black'}
                  direction="right"
                  to={`/projects/${data.nextProject.frontmatter.slug}`}
                >
                  <h3 className="project-pagination-next-title">
                    {data.nextProject.frontmatter.title}
                  </h3>
                </NavLink>
                <div
                  className="project-pagination-next-background"
                  style={{
                    backgroundImage: `url(${data.nextProject.frontmatter.featuredImage.childImageSharp.original.src})`,
                  }}
                ></div>
                <span className="direction">Next</span>
                <span className="right-arrow">&rarr;</span>
              </>
            )}
          </div>
        </div>
      </PaginationWrapper>
    </Layout>
  )
}

export default Project

export const pageQuery = graphql`
  query($slug: String!, $next: String, $previous: String!) {
    markdownRemark(frontmatter: { slug: { eq: $slug } }) {
      frontmatter {
        title
        slug
        client
        services {
          service
        }
        featuredImage {
          childImageSharp {
            fluid(maxWidth: 1480) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        images {
          image {
            childImageSharp {
              fluid(maxWidth: 1480) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
          image_alt
          half_image
        }
      }
      html
    }
    nextProject: markdownRemark(frontmatter: { slug: { eq: $next } }) {
      frontmatter {
        title
        slug
        featuredImage {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
    }
    previousProject: markdownRemark(frontmatter: { slug: { eq: $previous } }) {
      frontmatter {
        title
        slug
        featuredImage {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
    }
  }
`
